<template>
  <v-card flat>
    <v-card-text>
      <v-data-table
        v-model="selected"
        :headers="headers"
        :items="data"
        item-key="no_order"
        show-select
        class="elevation-1"
      >
        <template v-slot:item.status="{ item }">
          <div v-if="item.status == 1">
            Complete
          </div>
          <div v-else>
            Process
          </div>
        </template>
        <template v-slot:top>
          <v-toolbar flat>
            <v-menu bottom>
              <template v-slot:activator="{ on, attrs }">
                <v-btn
                  v-bind="attrs"
                  :loading="loading"
                  :disabled="selected.length === 0 ? true : loading"
                  v-on="on"
                >
                  Actions
                </v-btn>
              </template>
              <v-list>
                <v-list-item @click="driverList()">
                  <v-list-item-title>
                    Move to Delivery Assigned
                  </v-list-item-title>
                </v-list-item>
                <v-list-item @click="postStatus(6)">
                  <v-list-item-title>
                    Move to Canceled
                  </v-list-item-title>
                </v-list-item>
              </v-list>
            </v-menu>
            <v-divider
              class="mx-4"
              inset
              vertical
            />
            <v-text-field
              v-model="search"
              left
              class="mx-2"
              append-icon="mdi-magnify"
              label="Search"
              align-right
            />
          </v-toolbar>
          <v-dialog
            v-model="dialog"
            max-width="400px"
          >
            <v-card>
              <v-card-title
                class="primary"
                style="color:white"
              >
                Assign Driver
              </v-card-title>

              <v-card-text
                class="mt-6"
                style="color:black"
              >
                <v-select
                  v-model="selectedDriver"
                  :items="driver_list"
                  item-text="name"
                  item-value="id"
                  label="Choose an Driver"
                  outlined
                  clearable
                />
              </v-card-text>
              <v-card-actions>
                <v-spacer />
                <div>
                  <v-btn
                    color="primary"
                    text
                    @click="assignReturnDriver(9)"
                  >
                    Submit
                  </v-btn>
                </div>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </template>
        <template v-slot:item.actions="{ item }">
          <v-btn
            icon
            small
            @click="detailOrder(item.no_order)"
          >
            <v-icon>mdi-eye</v-icon>
          </v-btn>
        </template>
        <template v-slot:item.delivery_fee="{ item }">
          {{ formatThousands(item.delivery_fee, { separator: ',' }) }}
        </template>
      </v-data-table>
    </v-card-text>
    <detail-order-dialog
      v-show="this.$store.state.dialog"
      :data="detail"
    />
  </v-card>
</template>

<script>
  import axios from 'axios'
  import DetailOrderDialog from './DetailOrderDialog.vue'
  var formatThousands = require('format-thousands')
  export default {
    components: {
      DetailOrderDialog,
    },
    // eslint-disable-next-line vue/require-prop-types
    props: ['data'],
    data () {
      return {
        formatThousands,
        selected: [],
        search: '',
        dialog: false,
        loading: false,
        dialogField: false,
        sortable: false,
        detail: [],
        headers: [
          {
            text: 'No Order',
            value: 'no_order',
          },
          {
            text: 'Order Date',
            value: 'date',
          },
          {
            text: 'Client',
            value: 'client',
          },
          {
            text: 'Delivery Fee',
            value: 'delivery_fee',
          },
          // {
          //   text: 'Order Status',
          //   value: 'order_status',
          // },
          // {
          //   text: 'Payment Status',
          //   value: 'payment_status',
          // },
          {
            text: 'Pick Up Driver',
            value: 'pickup_driver',
          },
          {
            text: 'Delivery Driver',
            value: 'deliver_driver',
          },
          {
            text: 'Return Driver',
            value: 'return_driver',
          },
          {
            text: 'Payment Method',
            value: 'payment_method',
          },
          {
            text: 'Status',
            value: 'status',
          },
          {
            text: 'Actions',
            value: 'actions',
            sortable: false,
          },
        ],
      }
    },

    methods: {
      postStatus (status) {
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            status: status,
          }),
        )
        axios.post('admin/status', data).then(() => {
          this.refresh()
          this.selected = []
        })
      },

      refresh () {
        axios.get('admin/order/return').then(res => {
          this.data = res.data.data
        })
      },
      detailOrder (no) {
        // eslint-disable-next-line camelcase
        const noOrder = no.replace(/#/g, '')
        console.log(noOrder)
        axios.get('admin/order/detail/' + noOrder).then(res => {
          this.detail = res.data.data[0]
          this.$store.commit('dialog', true)
        })
      },

      driverList () {
        this.$swal.showLoading()
        const id = this.selected
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
          }),
        )
        axios.post('admin/change-driver/filter', data).then(res => {
          this.driver_list = res.data.data
          this.dialog = true
          this.$swal.close()
        })
      },

      assignReturnDriver () {
        const id = this.selected
        const d = this.selectedDriver
        const data = []
        id.forEach(item =>
          data.push({
            id: item.id,
            driver: d,
          }),
        )

        axios.post('admin/driver/return-assign', data).then(() => {
          this.refresh()
          this.$swal('Data Updated Successfully', '', 'success')
          this.dialog = false
        })
      },

    },
  }
</script>
